import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { minBy, sortBy } from 'lodash';
import { IOption } from 'ng-select';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { isNullOrUndefined } from 'util';
import {
  CreateJobDocumentRevisionInput,
  JobDefinitionDto,
  JobDocumentDto,
  JobDocumentInput,
  JobDocumentRevisionDto
} from '../../../proxies';
import { JobDefinitionDetailService } from '../job-definition-detail.service';

@Injectable()
export class JobDefinitionDocumentService {
  documentForm: FormGroup;

  documentTypes: IOption[] = [
    {value: 'xlsx', label: 'Excel'},
    {value: 'docx', label: 'Word'},
    {value: 'pptx', label: 'Powerpoint'},
    {value: 'pdf', label: 'Pdf'},
    {value: '*', label: 'Other'}
  ];

  private activeJobDocumentSubject: BehaviorSubject<JobDocumentInput> = new BehaviorSubject(new JobDocumentInput());

  constructor(private jobDefinitionService: JobDefinitionDetailService,
              private formBuilder: FormBuilder,
              private t: TranslateService) {
    this.activeJobDocumentSubject.subscribe((doc: JobDocumentDto) => {
      this.initDocumentForm(doc);
    });
  }

  get afterSuccessfulSave(): Observable<any> {
    return this.jobDefinitionService.afterSuccessfulSave;
  }

  get activeJobDefinition(): JobDefinitionDto {
    return this.jobDefinitionService.activeJobDefinition;
  }

  get activeJobDocument(): JobDocumentInput {
    return this.activeJobDocumentSubject.getValue();
  }

  set activeJobDocument(jobDocument: JobDocumentInput) {
    this.activeJobDocumentSubject.next(jobDocument);
  }

  get readOnly(): boolean {
    return this.jobDefinitionService.readOnly;
  }

  validateJobDefinition(): void {
    this.jobDefinitionService.validateJobDefinition();
  }

  get currentFile(): JobDocumentRevisionDto | CreateJobDocumentRevisionInput | null {
    const revision: CreateJobDocumentRevisionInput | undefined = this.activeJobDocument.newRevision;

    if (revision) {
      return revision;
    } else {
      const jobDocument: JobDocumentDto = this.activeJobDocument as JobDocumentDto;

      if (jobDocument.revisions.length > 0) {
        return jobDocument.revisions[ jobDocument.revisions.length - 1 ];
      }
    }

    return null;
  }


  get documentHistory(): Array<JobDocumentRevisionDto> {
    const document: JobDocumentDto = this.activeJobDocument as JobDocumentDto;
    const revisions: Array<JobDocumentRevisionDto> = sortBy(document.revisions, (rev: JobDocumentRevisionDto) => {
      return rev.creationTime;
    }).reverse();

    const hasNewRevision: boolean = !isNullOrUndefined(this.activeJobDocument.newRevision);

    return hasNewRevision ?
      revisions : revisions.slice(1);
  }

  initDocumentForm(doc: JobDocumentDto): void {
    this.documentForm = this.formBuilder.group({
      name: [ '', Validators.required ],
      description: [ '' ],
      documentType: [ 'xlsx', Validators.required ]
    });

    this.documentForm.patchValue(doc, {onlySelf: true});

    if (this.jobDefinitionService.readOnly) {
      this.documentForm.disable();
    }

    this.documentForm.valueChanges.subscribe(() => {
      this.jobDefinitionService.documentsHaveChanges = true;
      doc.name = this.documentForm.controls.name.value;
      doc.description = this.documentForm.controls.description.value;
      doc.documentType = this.documentForm.controls.documentType.value;
      this.jobDefinitionService.tabs[ 2 ].label = doc.name;
      this.jobDefinitionService.onChange();
    });
  }

  createDocument(): JobDocumentDto {
    const jobDocument: JobDocumentDto = new JobDocumentDto();
    jobDocument.revisions = [];
    const minDocumentId: number = this.jobDefinitionService.activeJobDefinition.documents.length ?
      (minBy(this.jobDefinitionService.activeJobDefinition.documents, (d: JobDocumentDto) => d.id) as JobDocumentDto).id :
      -1;

    jobDocument.id = minDocumentId < 0 ? minDocumentId - 1 : -1;
    jobDocument.documentType = 'xlsx';
    jobDocument.name = '';
    jobDocument.description = '';

    this.jobDefinitionService.documentsHaveChanges = true;
    this.jobDefinitionService.activeJobDefinition.documents.push(jobDocument);

    return jobDocument;
  }

  updateTabs(route: ActivatedRoute): void {
    const docId: number = route.snapshot.params[ 'documentId' ];

    const tabs: any [] = [
      this.jobDefinitionService.tabs[ 0 ],
      {
        label: this.t.instant('frigg.documents'),
        routerLink: [ 'documents' ]
      }
    ];

    if (docId) {
      tabs.push({
        label: this.activeJobDocument.name || this.t.instant('frigg.newDocument'),
        routerLink: [ 'documents/' + docId ]
      });
    }

    this.jobDefinitionService.tabs = tabs;
  }

  setActiveDocument(documentId: number): void {
    const document: JobDocumentDto | undefined = this.jobDefinitionService.activeJobDefinition.documents
      .find((t: JobDocumentDto) => {
        return t.id === +documentId;
      });

    if (document) {
      this.activeJobDocument = document as JobDocumentInput;
    }
  }

  toggleDeleteFlag(document: JobDocumentDto): void {
    document.deleteFlag = !document.deleteFlag;
    this.jobDefinitionService.documentsHaveChanges = true;
  }

  handleFileInput(fileInput: { file: File, binaryString: string }): void {
    this.activeJobDocument.newRevision = this.activeJobDocument.newRevision || new CreateJobDocumentRevisionInput();

    this.activeJobDocument.newRevision.fileName = fileInput.file.name;
    this.activeJobDocument.newRevision.fileDate = fileInput.file.lastModifiedDate;
    this.activeJobDocument.newRevision.fileContent = btoa(fileInput.binaryString);

    this.jobDefinitionService.documentsHaveChanges = true;
  }
}



