import { ValidationError } from 'class-validator'
import { JobTaskActionInput, JobTaskActionParameterInput, JobTaskInput } from '../../../../proxies';
import { BaseError } from './base-error';

export class ActionParameterError extends BaseError {
  task: JobTaskInput;
  action: JobTaskActionInput;
  parameter: JobTaskActionParameterInput;

  constructor(error: ValidationError, task: JobTaskInput, action: JobTaskActionInput, parameter: JobTaskActionParameterInput) {
    super();
    this.task = task;
    this.action = action;
    this.parameter = parameter;
    this.error = error;
  }
}



