import { ValidationError } from 'class-validator'

import { JobTaskActionInput, JobTaskInput } from '../../../../proxies';
import { BaseError } from './base-error';

export class ActionError extends BaseError {
  task: JobTaskInput;
  action: JobTaskActionInput;

  constructor(error: ValidationError, task: JobTaskInput, action: JobTaskActionInput) {
    super();
    this.action = action;
    this.task = task;
    this.error = error;
  }
}



