import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { minBy } from 'lodash';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { isNullOrUndefined } from 'util';
import { JobDefinitionDto, JobTaskDto } from '../../../proxies';
import { JobDefinitionDetailService } from '../job-definition-detail.service';

@Injectable()
export class JobDefinitionTaskService {
  private activeJobTaskSubject: BehaviorSubject<JobTaskDto> = new BehaviorSubject(new JobTaskDto());

  taskForm: FormGroup;

  userList: { value: string, label: string }[] = [];

  constructor(private jobDefinitionService: JobDefinitionDetailService,
              private formBuilder: FormBuilder,
              private t: TranslateService) {

    this.activeJobTaskSubject.subscribe((task: JobTaskDto) => {
      this.initTaskForm(task);
    });
  }

  get afterSuccessfulSave(): Observable<any> {
    return this.jobDefinitionService.afterSuccessfulSave;
  }

  get activeJobDefinition(): JobDefinitionDto {
    return this.jobDefinitionService.activeJobDefinition;
  }

  get activeJobTask(): JobTaskDto {
    return this.activeJobTaskSubject.getValue();
  }

  set activeJobTask(jobTask: JobTaskDto) {
    this.activeJobTaskSubject.next(jobTask);
  }

  get readOnly(): boolean {
    return this.jobDefinitionService.readOnly;
  }

  validateJobDefinition(): void {
    this.jobDefinitionService.validateJobDefinition();
  }

  initTaskForm(task: JobTaskDto): void {
    this.taskForm = this.formBuilder.group({
      name: [ '', Validators.required ],
      description: [ '' ],
      userId: [ '', Validators.required ],
      notifyByEmail: [ false ],
      notifyBySms: [ false ],
      daysToComplete: [ '' ]
    });

    this.userList = this.jobDefinitionService.userList;

    this.taskForm.patchValue({
      name: task.name,
      description: task.description,
      userId: isNullOrUndefined(task.userId) ? '' : task.userId.toString(),
      notifyByEmail: task.notifyByEmail,
      notifyBySms: task.notifyBySms,
      daysToComplete: task.daysToComplete
    });

    if (this.jobDefinitionService.readOnly) {
      this.taskForm.disable();
    }

    this.taskForm.valueChanges.subscribe(() => {
      task.name = this.taskForm.controls.name.value;
      task.description = this.taskForm.controls.description.value;
      task.notifyByEmail = this.taskForm.controls.notifyByEmail.value;
      task.notifyBySms = this.taskForm.controls.notifyBySms.value;
      task.daysToComplete = +this.taskForm.controls.daysToComplete.value;
      task.userId = this.taskForm.controls.userId.value === '' ?
        null :
        this.taskForm.controls.userId.value;
      this.jobDefinitionService.tasksHaveChanges = true;
      this.jobDefinitionService.tabs[ 2 ].label = task.name;

      this.jobDefinitionService.onChange();
    });
  }

  createTask(): JobTaskDto {
    const jobTask: JobTaskDto = new JobTaskDto();
    const minTaskId: number = this.activeJobDefinition.tasks.length ?
      (minBy(this.activeJobDefinition.tasks, (d: JobTaskDto) => d.id) as JobTaskDto).id :
      -1;

    jobTask.id = minTaskId < 0 ? minTaskId - 1 : -1;
    jobTask.name = '';
    jobTask.description = '';
    jobTask.daysToComplete = 0;
    jobTask.actions = [];

    this.jobDefinitionService.tasksHaveChanges = true;
    this.activeJobDefinition.tasks.push(jobTask);

    return jobTask;
  }

  updateTabs(route: ActivatedRoute): void {
    const taskId: number = route.snapshot.params[ 'taskId' ];
    const actionId: number = route.snapshot.params[ 'actionId' ];

    const tabs: any [] = [
      this.jobDefinitionService.tabs[ 0 ],
      {
        label: this.t.instant('frigg.tasks'),
        routerLink: [ 'tasks' ]
      }
    ];

    if (taskId) {
      tabs.push({
        label: this.activeJobTask.name || this.t.instant('frigg.newTask'),
        routerLink: [ 'tasks/' + taskId ]
      });
    }

    this.jobDefinitionService.tabs = tabs;
  }

  setActiveTask(taskId: number): JobTaskDto | undefined {
    const task: JobTaskDto | undefined = this.jobDefinitionService.activeJobDefinition.tasks
      .find((t: JobTaskDto) => {
        return t.id === +taskId;
      });

    if (task) {
      this.activeJobTaskSubject.next(task);
      this.initTaskForm(task);
    }

    return task;
  }

  toggleDeleteFlagTask(task: JobTaskDto): void {
    task.deleteFlag = !task.deleteFlag;
    this.jobDefinitionService.tasksHaveChanges = true;
  }
}




