import { AfterViewInit, Component, ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DOM } from 'rx-dom';
import { FromObservable } from 'rxjs/observable/FromObservable';
import { JobDocumentDto } from '../../../../proxies';
import { JobDefinitionDocumentService } from '../job-definition-document.service';

@Component({
  selector: 'frigg-job-definition-document-detail',
  templateUrl: 'job-definition-document-detail.component.html',
  styleUrls: [ 'job-definition-document-detail.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class JobDefinitionDocumentDetailComponent implements OnInit, AfterViewInit {
  documentForm: FormGroup;

  @ViewChild('fileInput') fileInput: ElementRef;

  constructor(public documentService: JobDefinitionDocumentService,
              private router: Router,
              private route: ActivatedRoute,
              private t: TranslateService) {

    documentService.afterSuccessfulSave.subscribe(() => {
      this.afterSuccessfulSave();
    });
  }

  get fileButtonText(): string {
    const hasNewRevision: boolean = this.documentService.activeJobDocument.newRevision === null;
    const document: JobDocumentDto = this.documentService.activeJobDocument as JobDocumentDto;

    return (hasNewRevision || document.revisions.length > 0) ?
      this.t.instant('frigg.replaceFile') :
      this.t.instant('frigg.addFile');
  }

  ngOnInit(): void {
    this.route.params.subscribe((param: { documentId: string }) => {
      this.documentService.setActiveDocument(+param.documentId);
      this.documentService.updateTabs(this.route);

      const fromAdd = this.route.snapshot.queryParams && this.route.snapshot.queryParams[ 'fromAdd' ];
      if (!fromAdd) {
        this.documentService.validateJobDefinition();
      }
    });
  }

  ngAfterViewInit(): void {
    DOM.change(this.fileInput.nativeElement)
      .flatMap((event: Event): any => {
        const inputEl: HTMLInputElement = event.target as HTMLInputElement;
        return FromObservable.create(inputEl.files || []);
      })
      .flatMap((file: File) => {
        const xDOM: any = DOM as any;
        return xDOM.fromReader(file).asBinaryString().map((
          (binaryString: string): { file: File, binaryString: string } => {
            return {file, binaryString};
          }
        ));
      })
      .subscribe((result: { file: File, binaryString: string }) => {
        this.documentService.handleFileInput(result);
      });
  }

  onAddFileClick(): void {
    this.fileInput.nativeElement.click();
  }

  onUndoFileClick(): void {
    this.documentService.activeJobDocument.newRevision = undefined;
  }

  private afterSuccessfulSave(): void {
    const documentId: number = this.documentService.activeJobDocument.id;

    documentId > 0 ?
      this.documentService.setActiveDocument(documentId) :
      this.router.navigate([
          'jobdefinitions',
          this.documentService.activeJobDefinition.id,
          'documents'
        ],
        {replaceUrl: true});
  }
}




