import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreService } from '../../core/core.service';
import { FriggResponse, JobDefinitionDto, JobDefinitionListDto, JobDefinitionService, ListResultDto } from '../../proxies';
import { BaseError } from '../detail/validation/errors/base-error';
import { JobDefinitionValidationService } from '../detail/validation/job-definition-validation.service';
import { JobDefinitionStatusService } from '../job-definition-status.service';

@Component({
  selector: 'frigg-job-definition-list',
  templateUrl: 'job-definition-list.component.html',
  styleUrls: [ 'job-definition-list.component.scss' ]
})
export class JobDefinitionListComponent implements OnInit {
  jobs: Array<JobDefinitionListDto> = [];

  constructor(private core: CoreService,
              private statusService: JobDefinitionStatusService,
              private jobService: JobDefinitionService,
              private validationService: JobDefinitionValidationService,
              private router: Router,
              private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.core.setTranslatedShellHeader('frigg.jobs');
    this.core.setTranslatedNavigateText('frigg.homePage');
    this.core.shell.setNavigateUrl('/dashboard');

    this.route.data
      .subscribe((data: { jobs: ListResultDto<JobDefinitionListDto> }) => {
        this.jobs = data.jobs.items;
      });
  }

  onItemClick(job: JobDefinitionListDto): void {
    this.router.navigate([ `./jobdefinitions/${ job.id }` ]);
  }

  onStartClick(job: JobDefinitionListDto): void {
    this.statusService.startJob(job.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          job.status = newJob.status;
        }
      });
  }

  onContinueClick(job: JobDefinitionListDto): void {
    this.statusService.continueJob(job.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          job.status = newJob.status;
        }
      });
  }

  onPauseClick(job: JobDefinitionListDto): void {
    this.statusService.pauseJob(job.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          job.status = newJob.status;
        }
      });
  }

  onRestartClick(job: JobDefinitionListDto): void {
    this.statusService.restartJob(job.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          job.status = newJob.status;
        }
      });
  }

  onAbortClick(job: JobDefinitionListDto): void {
    this.statusService.abortJob(job.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          job.status = newJob.status;
        }
      });
  }

  onShowProblemClick(job: JobDefinitionListDto): void {
    this.jobService.get(job.id)
      .subscribe((response: FriggResponse<JobDefinitionDto>) => {
        if (response.success) {
          this.validationService.validateJobDefinition(response.result).then((errors: Array<BaseError>) => {
            if (errors.length) {
              this.validationService.showValidationDialog(job.id, errors)
                .subscribe((url: string) => {
                  if (url && url.length) {
                    this.router.navigate([ url ]);
                  }
                });
            }
          });
        }
      });
  }
}



