import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
import { ListResultDto, UserListDto, UserService } from '../../../proxies';
import { FriggResponse } from '../../../proxies/';

@Injectable()
export class JobDefinitionUserListResolver implements Resolve<Array<{ value: string, label: string }>> {
  constructor(private userService: UserService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Array<{ value: string, label: string }>> {
    return this.userService.getUserList()
      .filter((response: FriggResponse<ListResultDto<UserListDto>>) => {
        return response.success;
      })
      .map((response: FriggResponse<ListResultDto<UserListDto>>) => {
        return response.result.items.map((user: UserListDto) => {
          return {value: user.id.toString(), label: `${user.name} ${user.surname}`};
        });
      });
  }
}



