import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, JobDefinitionListDto, JobDefinitionService, ListResultDto } from '../../../proxies';

@Injectable()
export class JobDefinitionListResolver implements Resolve<ListResultDto<JobDefinitionListDto>> {
  constructor(private jobDefinitionService: JobDefinitionService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ListResultDto<JobDefinitionListDto>> {
    return this.jobDefinitionService.getList()
      .map((response: FriggResponse<ListResultDto<JobDefinitionListDto>>) => {
        if (response.success) {
          return response.result;
        } else {
          return new ListResultDto<JobDefinitionListDto>();
        }
      });
  }
}



