import { animate, group, query, style, transition, trigger } from '@angular/animations';
import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, CanDeactivate, Router, RouterOutlet, RouterStateSnapshot } from '@angular/router';
import 'rxjs/add/operator/take';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../core/core.service';
import { FriggResponse, JobDefinitionDto } from '../../proxies';
import { JobDefinitionStatusService } from '../job-definition-status.service';
import { TaskActionService } from './action/task-action.service';
import { JobDefinitionDocumentService } from './document/job-definition-document.service';
import { JobDefinitionDetailService } from './job-definition-detail.service';
import { JobDefinitionSetupComponent } from './setup/job-definition-setup.component';
import { JobDefinitionTaskService } from './task/job-definition-task.service';
import { JobDefinitionTaskListComponent } from './task/list/job-definition-task-list.component';
import { BaseError } from './validation/errors/base-error';

@Component({
  selector: 'frigg-job-definition-detail',
  templateUrl: 'job-definition-detail.component.html',
  styleUrls: [ 'job-definition-detail.component.scss' ],
  animations: [
    trigger('routeAnimation', [
      transition('1=>2,1=>3,1=>4,2=>3,2=>4,3=>4', [
        style({height: '!', overflowX: 'hidden'}),
        query(':enter', style({transform: 'translateX(100%)'})),
        query(':enter, :leave', style({position: 'absolute', top: 0, left: 0, right: 0})),
        group([
          query(':leave', [ animate('0.3s cubic-bezier(.35, 0, .25, 1)', style({transform: 'translateX(-100%)'})) ]),
          query(':enter', [ animate('0.3s cubic-bezier(.35, 0, .25, 1)', style({transform: 'translateX(0)'})) ])
        ]),
        style({height: '!', overflowX: 'auto'})
      ]),
      transition('4=>3,4=>2,4=>1,3=>2,3=>1,2=>1', [
        style({height: '!', overflowX: 'hidden'}),
        query(':enter', style({transform: 'translateX(-100%)'})),
        query(':enter, :leave', style({position: 'absolute', top: 0, left: 0, right: 0})),
        group([
          query(':leave', [ animate('0.3s cubic-bezier(.35, 0, .25, 1)', style({transform: 'translateX(100%)'})) ]),
          query(':enter', [ animate('0.3s cubic-bezier(.35, 0, .25, 1)', style({transform: 'translateX(0)'})) ])
        ]),
        style({height: '!', overflowX: 'auto'})
      ])
    ])
  ],
  encapsulation: ViewEncapsulation.None,
  providers: [
    JobDefinitionDetailService,
    JobDefinitionDocumentService,
    JobDefinitionTaskService,
    TaskActionService
  ]
})
export class JobDefinitionDetailComponent implements OnInit, CanDeactivate<JobDefinitionDetailComponent> {
  @ViewChild('jobDefinitionSetup') jobDefinitionSetup: JobDefinitionSetupComponent;
  @ViewChild('jobDefinitionTasks') jobDefinitionTasks: JobDefinitionTaskListComponent;

  constructor(public viewService: JobDefinitionDetailService,
              private statusService: JobDefinitionStatusService,
              private router: Router,
              private route: ActivatedRoute,
              private core: CoreService) {

  }

  ngOnInit(): void {
    this.core.setTranslatedNavigateText('frigg.jobs');
    this.core.shell.setNavigateUrl('/jobdefinitions');

    this.route.data
      .subscribe((data: { job: JobDefinitionDto, users: Array<{ value: string, label: string }> }) => {
        this.viewService.activeJobDefinition = data.job;
        this.viewService.userList = data.users;
        this.core.shell.setHeader(this.viewService.activeJobDefinition.name);
      });
  }

  getDepth(outlet: RouterOutlet): number {
    return outlet.activatedRouteData[ 'depth' ];
  }

  get problemText(): string {
    return this.core.translate(
      this.viewService.errors.length === 1 ?
        'frigg.oneProblem' :
        'frigg.multipleProblems',
      {count: this.viewService.errors.length}
    );
  }

  onDeleteClick(): void {
    if (this.viewService.activeJobDefinition.id !== 0) {
      this.core.dialog.confirm(this.core.translate('frigg.deleteJobDefinitionConfirmation', {
          description: this.viewService.activeJobDefinition.name
        }
      )).subscribe((confirm: boolean) => {
        if (confirm) {
          this.viewService.deleteJobDefinition()
            .subscribe((result: boolean) => {
              if (result) {
                this.router.navigate([ 'jobdefinitions' ], {replaceUrl: true});
                this.core.toast.success(this.core.translate('frigg.jobDeleted'));
              }
            });
        }
      });
    }
  }

  onSaveClick(): void {
    const isNew: boolean = this.viewService.activeJobDefinition.id === 0;
    const successMessage: string = isNew ?
      this.core.translate('frigg.jobCreated') :
      this.core.translate('frigg.jobUpdated');

    this.viewService.validateJobDefinition();
    if (this.viewService.errors.filter((e: BaseError) => e.critical).length > 0) {
      this.core.toast.warning(this.core.translate('frigg.jobDefinitionSaveCanceled'));
      return;
    }

    this.viewService.saveJobDefinition()
      .subscribe((response: FriggResponse<JobDefinitionDto>) => {
        if (response.success) {
          this.core.toast.success(successMessage);
          if (isNew) {
            this.router.navigate([ `jobdefinitions/${ this.viewService.activeJobDefinition.id }` ], {replaceUrl: true});
          }
        } else {
          this.core.toast.error(response.error.details, response.error.message);
        }
      });
  }

  canDeactivate(component: JobDefinitionDetailComponent,
                route: ActivatedRouteSnapshot,
                state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    if (!this.viewService.isDirty) {
      return true;
    }

    return this.core.dialog
      .confirm(
        this.core.translate('frigg.unsavedChangesInJobDefinition'),
        this.core.translate('frigg.confirmNavigationFromUnsavedJobDefinition'))
      .take(1);
  }

  onErrorButtonClick(): void {
    this.viewService.showValidationDialog()
      .subscribe((url: string) => {
        if (url && url.length) {
          this.router.navigate([ url ]);
        }
      })
  }

  get statusButtonInfo(): { text: string, icon: string } {
    switch (this.viewService.activeJobDefinition.status) {
      case 'Ready': {
        return {
          text: this.core.translate('frigg.startJob'),
          icon: 'play_arrow'
        };
      }
      case 'Active': {
        return {
          text: this.core.translate('frigg.putJobOnHold'),
          icon: 'pause'
        };
      }
      case 'OnHold': {
        return {
          text: this.core.translate('frigg.continueJob'),
          icon: 'play_arrow'
        };
      }
      case 'Aborted':
      case 'Completed': {
        return {
          text: this.core.translate('frigg.prepareNewStartOfJob'),
          icon: 'fast_rewind'
        };
      }
    }

    return {text: '', icon: ''};
  }

  onAbortClick(): void {
    this.statusService.abortJob(this.viewService.activeJobDefinition.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          this.viewService.activeJobDefinition = newJob;
          this.viewService.afterSuccessfulSave.next();
        }
      });
  }

  onStatusButtonClick(): void {
    this.statusFunction(this.viewService.activeJobDefinition.id)
      .subscribe((newJob: JobDefinitionDto) => {
        if (newJob) {
          this.viewService.activeJobDefinition = newJob;
          this.viewService.afterSuccessfulSave.next();
        }
      });
  }

  private get statusFunction(): (jobId: number) => Observable<JobDefinitionDto | null> {
    switch (this.viewService.activeJobDefinition.status) {
      case 'Ready': {
        return this.statusService.startJob.bind(this.statusService);
      }
      case 'Active': {
        return this.statusService.pauseJob.bind(this.statusService);
      }
      case 'OnHold': {
        return this.statusService.continueJob.bind(this.statusService);
      }
      case 'Aborted':
      case 'Completed': {
        return this.statusService.restartJob.bind(this.statusService);
      }
    }

    return (): Observable<JobDefinitionDto | null> => Observable.of(null);
  }

}



