import { Component, Input } from '@angular/core';
import { JobTaskDto } from '../../../../proxies';
import { JobDefinitionDetailService } from '../../job-definition-detail.service';

@Component({
  selector: 'frigg-task-tile',
  templateUrl: 'task-tile.component.html',
  styleUrls: [ 'task-tile.component.scss' ]
})
export class TaskTileComponent {
  @Input() task: JobTaskDto;

  constructor(private jobDefinitionDetailService: JobDefinitionDetailService) {
  }

  get userName(): string {
    const user: { value: string, label: string } | undefined = this.jobDefinitionDetailService.userList
      .find((u: any) => +u.value === +this.task.userId);

    return user ? user.label : '';
  }
}



