import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { JobDocumentDto } from '../../../../proxies';
import { JobDefinitionDocumentService } from '../job-definition-document.service';

@Component({
  selector: 'frigg-job-definition-document-list',
  templateUrl: 'job-definition-document-list.component.html',
  styleUrls: [ 'job-definition-document-list.component.scss' ],
  encapsulation: ViewEncapsulation.None,
})
export class JobDefinitionDocumentListComponent implements OnInit {
  constructor(public documentService: JobDefinitionDocumentService,
              private route: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit(): void {
    this.documentService.updateTabs(this.route);
    this.documentService.validateJobDefinition();
  }

  onAddDocumentClick(): void {
    const document: JobDocumentDto = this.documentService.createDocument();
    this.router.navigate([
      'jobdefinitions',
      this.documentService.activeJobDefinition.id,
      'documents',
      document.id ], {
      queryParams: {
        fromAdd: true
      }
    });
  }

  onDeleteDocumentClick(document: JobDocumentDto): void {
    this.documentService.toggleDeleteFlag(document);
    this.documentService.validateJobDefinition();
  }

  onDocumentClick(document: JobDocumentDto): void {
    this.router.navigate([
      'jobdefinitions',
      this.documentService.activeJobDefinition.id,
      'documents',
      document.id
    ]);
  }
}





