import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { remove as lodashRemove } from 'lodash';
import 'rxjs/add/operator/debounceTime'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { CoreService } from '../../core/core.service';
import {
  FriggResponse,
  JobDefinitionDto,
  JobDefinitionInput,
  JobDefinitionService,
  JobDocumentDto,
  JobTaskActionDto,
  JobTaskDto
} from '../../proxies';
import { BaseError } from './validation/errors/base-error';
import { JobDefinitionValidationService } from './validation/job-definition-validation.service';

interface ITab {
  label: string;
  icon?: string;
  routerLink?: string [];
}

@Injectable()
export class JobDefinitionDetailService {
  private activeJobDefinitionSubject: BehaviorSubject<JobDefinitionDto>;

  tabsSubject: BehaviorSubject<Array<ITab>>;

  get tabs(): Array<ITab> {
    return this.tabsSubject.getValue();
  };

  // TODO: Introduced after upgrade to angular 4.2.2 due to change detection issues
  // ref: https://github.com/angular/angular/issues/17572
  set tabs(v: Array<ITab>) {
    setTimeout(() => this.tabsSubject.next(v), 0);
  }

  afterSuccessfulSave: Subject<any> = new Subject<any>();

  setupForm: FormGroup;

  documentsHaveChanges = false;
  tasksHaveChanges = false;

  userList: { value: string, label: string }[] = [];

  private onChangeSubject: Subject<any> = new Subject<any>();
  errors: Array<BaseError> = [];

  constructor(private jobDefinitionService: JobDefinitionService,
              private core: CoreService,
              private formBuilder: FormBuilder,
              private validationService: JobDefinitionValidationService) {
    const dummyJob: JobDefinitionDto = new JobDefinitionDto();
    dummyJob.tasks = [];
    dummyJob.documents = [];

    this.activeJobDefinitionSubject = new BehaviorSubject(dummyJob);

    this.tabsSubject = new BehaviorSubject<Array<ITab>>([
      {
        label: this.core.translate('frigg.job'),
        routerLink: [ 'setup' ]
      }
    ]);

    this.activeJobDefinitionSubject.subscribe((jobDefinition: JobDefinitionDto) => {
      this.initSetupForm(jobDefinition);
    });

    this.onChangeSubject
      .debounceTime(300)
      .subscribe(() => {
        this.validateJobDefinition();
      });
  }

  private initSetupForm(jobDefinition: JobDefinitionDto): void {
    this.setupForm = this.formBuilder.group({
      name: [ '', Validators.required ],
      description: [ '' ],
      isTemplate: [ false ]
    });

    this.setupForm.patchValue(jobDefinition, {onlySelf: true});
    if (this.readOnly) {
      this.setupForm.disable();
    }

    this.setupForm.valueChanges.subscribe(() => {
      jobDefinition.name = this.setupForm.controls.name.value;
      jobDefinition.description = this.setupForm.controls.description.value;
      jobDefinition.isTemplate = this.setupForm.controls.isTemplate.value;
      this.onChange();
    });
  }

  get activeJobDefinition(): JobDefinitionDto {
    return this.activeJobDefinitionSubject.getValue();
  }

  set activeJobDefinition(jobDefinition: JobDefinitionDto) {
    this.activeJobDefinitionSubject.next(jobDefinition);
    this.markAsPristine();
  }

  get readOnly(): boolean {
    return this.activeJobDefinition.status !== 'Draft' &&
      this.activeJobDefinition.status !== 'Ready' &&
      this.activeJobDefinition.status !== 'OnHold';
  }

  saveJobDefinition(): Observable<FriggResponse<JobDefinitionDto>> {
    return this.prepareJobDefinitionForSave()
      .flatMap((jobDefinition: JobDefinitionDto): Observable<FriggResponse<JobDefinitionDto>> => {
          return jobDefinition.id === 0 ?
            this.jobDefinitionService.create(jobDefinition as JobDefinitionInput) :
            this.jobDefinitionService.update(jobDefinition as JobDefinitionInput);
        }
      )
      .map((response: FriggResponse<JobDefinitionDto>) => {
        if (response.success) {
          this.activeJobDefinitionSubject.next(response.result);
          this.markAsPristine();
          this.afterSuccessfulSave.next();
        }
        return response;
      });
  }

  private prepareJobDefinitionForSave(): Observable<JobDefinitionDto> {
    const jobDefinition: JobDefinitionDto = this.activeJobDefinition;

    lodashRemove(jobDefinition.documents, (doc: JobDocumentDto) => {
      return doc.id < 0 && doc.deleteFlag;
    });

    lodashRemove(jobDefinition.tasks, (task: JobTaskDto) => {
      return task.id < 0 && task.deleteFlag;
    });

    jobDefinition.tasks.forEach((task: JobTaskDto) => {
      lodashRemove(task.actions, (action: JobTaskActionDto) => {
        return action.id < 0 && action.deleteFlag;
      });
    });

    return Observable.of(jobDefinition);
  }

  deleteJobDefinition(): Observable<boolean> {
    return this.jobDefinitionService
      .delete(this.activeJobDefinition.id)
      .map(() => {
        return true;
      });
  }

  get canSave(): boolean {
    const acceptableErrors: Array<BaseError> = this.activeJobDefinition.status === 'OnHold' ?
      this.errors : this.errors.filter((e: BaseError) => e.critical);

    return this.isDirty && acceptableErrors.length === 0;
  }

  get isValid(): boolean {
    return this.errors.length === 0;
  }

  get isDirty(): boolean {
    return this.setupForm.dirty ||
      this.documentsHaveChanges ||
      this.tasksHaveChanges;
  }

  markAsPristine(): void {
    this.setupForm.markAsPristine();
    this.documentsHaveChanges = false;
    this.tasksHaveChanges = false;
  }

  onChange(): void {
    this.core.shell.setHeader(this.activeJobDefinition.name);
    this.onChangeSubject.next();
  }

  validateJobDefinition(): void {
    this.validationService.validateJobDefinition(this.activeJobDefinition)
      .then((errors: Array<BaseError>) => {
        this.errors = errors;
      });
  }

  showValidationDialog(): Observable<string | undefined> {
    return this.validationService.showValidationDialog(this.activeJobDefinition.id, this.errors);
  }

  get statusText(): string {
    return `${this.core.translate('frigg.status')}: ${ this.core.translate('frigg.status_' + this.activeJobDefinition.status)}`
  }
}



