import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../../../core/core.service';
import { TaskActionService } from '../task-action.service';


@Component({
  selector: 'frigg-task-action-detail',
  templateUrl: 'task-action-detail.component.html',
  styleUrls: [ 'task-action-detail.component.scss' ]
})
export class TaskActionDetailComponent implements OnInit {
  actionCommentTexts = [
    'frigg.commentOptionNotAvailable',
    'frigg.commentOptionOptional',
    'frigg.commentOptionRequired'
  ];

  constructor(public actionService: TaskActionService,
              private core: CoreService,
              private router: Router,
              private route: ActivatedRoute) {
    actionService.afterSuccessfulSave.subscribe(() => {
      this.afterSuccessfulSave();
    });
  }

  get actionCommentLabel(): Observable<string> {
    return this.core.translation.get('frigg.commentIs');
  }

  get activeActionCommentText(): Observable<string> {
    if (!this.actionService.activeTaskAction || this.actionService.activeTaskAction.actionComment === undefined) {
      return Observable.of('');
    }
    return this.getActionCommentText(this.actionService.activeTaskAction.actionComment);
  }

  getActionCommentText(index: number): Observable<string> {
    return this.core.translation.get(this.actionCommentTexts[ index ]);
  }

  afterSuccessfulSave(): void {
    const taskId: number = this.actionService.activeJobTask.id;
    const actionId: number = this.actionService.activeTaskAction.id;

    actionId > 0 ?
      this.actionService.setActiveTaskAction(taskId, actionId) :
      this.router.navigate([
          'jobdefinitions',
          this.actionService.activeJobDefinition.id,
          'tasks',
          this.actionService.activeJobTask.id
        ],
        {replaceUrl: true});
  }

  ngOnInit(): void {
    this.route.params.subscribe((param: { taskId: string, actionId: string }) => {
      this.actionService.setActiveTaskAction(+param.taskId, +param.actionId);
      this.actionService.updateTabs(this.route);

      const fromAdd: boolean = this.route.snapshot.queryParams && this.route.snapshot.queryParams[ 'fromAdd' ];
      if (!fromAdd) {
        this.actionService.validateJobDefinition();
      }
    });
  }
}




