import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { JobTaskActionDto } from '../../../../proxies';
import { ActionTypeListService } from '../../action/action-type-list.service';
import { TaskActionService } from '../../action/task-action.service';
import { JobDefinitionTaskService } from '../job-definition-task.service';

@Component({
  selector: 'frigg-job-definition-task-detail',
  templateUrl: 'job-definition-task-detail.component.html',
  styleUrls: [ 'job-definition-task-detail.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class JobDefinitionTaskDetailComponent implements OnInit {
  get nonFinishingActions(): JobTaskActionDto[] {
    return this.taskService.activeJobTask.actions
      .filter((action: JobTaskActionDto) => this.actionTypeListService.isNonFinishing(action.actionType));
  }

  get finishingActions(): JobTaskActionDto[] {
    return this.taskService.activeJobTask.actions
      .filter((action: JobTaskActionDto) => this.actionTypeListService.isFinishing(action.actionType));
  }

  constructor(public taskService: JobDefinitionTaskService,
              private actionService: TaskActionService,
              public actionTypeListService: ActionTypeListService,
              private router: Router,
              private route: ActivatedRoute) {

    taskService.afterSuccessfulSave.subscribe(() => {
      this.afterSuccessfulSave();
    });
  }

  private afterSuccessfulSave(): void {
    const taskId: number = this.taskService.activeJobTask.id;

    taskId > 0 ?
      this.taskService.setActiveTask(taskId) :
      this.router.navigate([
          'jobdefinitions',
          this.taskService.activeJobDefinition.id,
          'tasks'
        ],
        {replaceUrl: true});
  }

  ngOnInit(): void {
    this.route.params.subscribe((param: { taskId: string }) => {
      const parentRoute: ActivatedRoute | null = this.route.parent;
      const jobId: string = parentRoute ? parentRoute.snapshot.params[ 'jobId' ] : '';
      this.taskService.setActiveTask(+param.taskId);
      this.taskService.updateTabs(this.route);

      const fromAdd = this.route.snapshot.queryParams && this.route.snapshot.queryParams[ 'fromAdd' ];
      if (!fromAdd) {
        this.taskService.validateJobDefinition();
      }
    });
  }

  onAddNonFinishingActionClick(): void {
    this.actionService.createNonFinishingTaskAction()
      .subscribe((action: JobTaskActionDto) => {
        this.navigateToAction(action, true);
      });
  }

  onAddFinishingActionClick(): void {
    this.actionService.createFinishingTaskAction()
      .subscribe((action: JobTaskActionDto) => {
        this.navigateToAction(action, true);
      });
  }

  private navigateToAction(action: JobTaskActionDto, fromAdd: boolean = false): void {
    if (action) {
      this.actionService.validateJobDefinition();
      this.router.navigate([
        'jobdefinitions', this.taskService.activeJobDefinition.id,
        'tasks', this.taskService.activeJobTask.id,
        'actions', action.id
      ], fromAdd ? {queryParams: {fromAdd: true}} : {});
    }
  }

  onActionClick(action: JobTaskActionDto): void {
    this.navigateToAction(action);
  }

  onDeleteActionClick(action: JobTaskActionDto): void {
    this.actionService.toggleDeleteFlagAction(action);
  }
}




