import { ValidationError } from 'class-validator'
import { JobDefinitionInput } from '../../../../proxies';
import { BaseError } from './base-error';

export class JobDefinitionError extends BaseError {
  job: JobDefinitionInput;

  constructor(error: ValidationError, job: JobDefinitionInput) {
    super();
    this.job = job;
    this.error = error;
  }
}



