import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { JobDefinitionDetailService } from '../job-definition-detail.service';

@Component({
  selector: 'frigg-job-definition-setup',
  templateUrl: 'job-definition-setup.component.html',
  styleUrls: [ 'job-definition-setup.component.scss' ]
})
export class JobDefinitionSetupComponent implements OnInit {
  constructor(public viewService: JobDefinitionDetailService,
              private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.viewService.tabs = this.viewService.tabs.slice(0, 1);
    const fromAdd: boolean = this.route.snapshot.queryParams[ 'fromAdd' ];

    if (!fromAdd) {
      this.viewService.validateJobDefinition();
    }
  }
}




