import { Component, Input, ViewEncapsulation } from '@angular/core';
import { JobDocumentDto, JobTaskDto, TaskActionDto } from '../../../../../proxies';
import { BaseError } from '../../errors/base-error';

@Component({
  selector: 'frigg-error-tile',
  templateUrl: './error-tile.component.html',
  styleUrls: [ './error-tile.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class ErrorTileComponent {

  @Input() document: JobDocumentDto;
  @Input() task: JobTaskDto;
  @Input() action: TaskActionDto;

  @Input() error: BaseError;

  constructor() {
  }
}



