import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { JobTaskDto } from '../../../../proxies';
import { JobDefinitionTaskService } from '../job-definition-task.service';

@Component({
  selector: 'frigg-job-definition-task-list',
  templateUrl: 'job-definition-task-list.component.html',
  styleUrls: [ 'job-definition-task-list.component.scss' ],
  encapsulation: ViewEncapsulation.None,
})
export class JobDefinitionTaskListComponent implements OnInit {

  constructor(public taskService: JobDefinitionTaskService,
              private route: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit(): void {
    this.taskService.updateTabs(this.route);
    this.taskService.validateJobDefinition();
  }

  onAddTaskClick(): void {
    const task: JobTaskDto = this.taskService.createTask();
    this.router.navigate([
      'jobdefinitions',
      this.taskService.activeJobDefinition.id,
      'tasks', task.id
    ], {
      queryParams: {
        fromAdd: true
      }
    });
  }

  onDeleteTaskClick(task: JobTaskDto): void {
    this.taskService.toggleDeleteFlagTask(task);
    this.taskService.validateJobDefinition();
  }

  onTaskClick(task: JobTaskDto): void {
    this.router.navigate([
      'jobdefinitions',
      this.taskService.activeJobDefinition.id,
      'tasks',
      task.id ]);
  }

}





