import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from '../core/can-deactivate-guard/can-deactivate-guard.service';
import { TaskActionDetailComponent } from './detail/action/detail/task-action-detail.component';
import { JobDefinitionDocumentDetailComponent } from './detail/document/detail/job-definition-document-detail.component';
import { JobDefinitionDocumentListComponent } from './detail/document/list/job-definition-document-list.component';
import { JobDefinitionDetailComponent } from './detail/job-definition-detail.component';
import { JobDefinitionResolver } from './detail/resolver/job-definition-resolver.service';
import { JobDefinitionUserListResolver } from './detail/resolver/job-definition-userlist-resolver.service';
import { JobDefinitionSetupComponent } from './detail/setup/job-definition-setup.component';
import { JobDefinitionTaskDetailComponent } from './detail/task/detail/job-definition-task-detail.component';
import { JobDefinitionTaskListComponent } from './detail/task/list/job-definition-task-list.component';
import { JobDefinitionListComponent } from './list/job-definition-list.component';
import { JobDefinitionListResolver } from './list/resolver/job-definition-list-resolver.service';

const routes: Routes = [
  {
    path: '', component: JobDefinitionListComponent, data: {title: 'Jobs'},
    resolve: {
      jobs: JobDefinitionListResolver
    },
  },
  {
    path: ':jobId', component: JobDefinitionDetailComponent, data: {title: 'Job Details'},
    resolve: {
      job: JobDefinitionResolver,
      users: JobDefinitionUserListResolver
    },
    canDeactivate: [ CanDeactivateGuard ],
    children: [
      {path: '', component: JobDefinitionSetupComponent, canDeactivate: [ CanDeactivateGuard ], data: {depth: 1}},
      {path: 'setup', redirectTo: ''},
      {path: 'tasks', component: JobDefinitionTaskListComponent, data: {depth: 2}},
      {
        path: 'tasks/:taskId',
        component: JobDefinitionTaskDetailComponent,
        canDeactivate: [ CanDeactivateGuard ],
        data: {depth: 3}
      },
      {
        path: 'tasks/:taskId/actions/:actionId',
        component: TaskActionDetailComponent,
        canDeactivate: [ CanDeactivateGuard ],
        data: {depth: 4}
      },
      {path: 'documents', component: JobDefinitionDocumentListComponent, data: {depth: 2}},
      {
        path: 'documents/:documentId',
        component: JobDefinitionDocumentDetailComponent,
        canDeactivate: [ CanDeactivateGuard ],
        data: {depth: 3}
      }
    ]
  }

];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ],
})
export class JobDefinitionRoutingModule {
}

export const routedComponents: Array<any> = [
  JobDefinitionDetailComponent,
  JobDefinitionSetupComponent,
  JobDefinitionTaskListComponent,
  JobDefinitionTaskDetailComponent,
  TaskActionDetailComponent,
  JobDefinitionDocumentListComponent,
  JobDefinitionDocumentDetailComponent
];



