import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
import { registerSchema, validate, ValidationError } from 'class-validator';
import { Observable } from 'rxjs/Observable';
import {
  JobDefinitionInput,
  JobDefinitionInputSchema,
  JobDocumentInput,
  JobDocumentInputSchema,
  JobTaskActionInput,
  JobTaskActionInputSchema,
  JobTaskActionParameterInput,
  JobTaskActionParameterInputSchema,
  JobTaskInput,
  JobTaskInputSchema
} from '../../../proxies/';
import { ActionTypeListService } from '../action/action-type-list.service';
import { ActionError } from './errors/action-error';
import { ActionParameterError } from './errors/action-parameter-error';
import { BaseError } from './errors/base-error';
import { DocumentError } from './errors/document-error';
import { JobDefinitionError } from './errors/job-definition-error';
import { TaskError } from './errors/task-error';
import { ValidationDialogComponent } from './validation-dialog/validation-dialog.component';

@Injectable()
export class JobDefinitionValidationService {

  constructor(private t: TranslateService,
              private matDialog: MatDialog,
              private actionTypeService: ActionTypeListService) {
    [ JobDefinitionInputSchema, JobDocumentInputSchema, JobTaskActionInputSchema,
      JobTaskActionParameterInputSchema, JobTaskInputSchema ].forEach(registerSchema);
  }

  async validateJobDefinition(jobDefinition: JobDefinitionInput): Promise<Array<BaseError>> {
    const errors = [];

    const jobDefinitionErrors = await validate(JobDefinitionInputSchema.name, jobDefinition);

    errors.push(
      ...jobDefinitionErrors
        .map((error: ValidationError) => new JobDefinitionError(error, jobDefinition))
        .map((baseError: BaseError) => {
          return this.translateError(baseError);
        }),
      ...this.validateMissingTasks(jobDefinition)
    );


    for (const task of jobDefinition.tasks.filter((t: JobTaskInput) => !t.deleteFlag)) {
      errors.push(...await this.validateTask(jobDefinition, task));
    }
    jobDefinition.documents
      .filter((document: JobDocumentInput) => !document.deleteFlag)
      .forEach((document: JobDocumentInput) => this.validateDocument(document, errors));

    return errors;
  }

  private async validateTask(jobDefinition: JobDefinitionInput, task: JobTaskInput): Promise<Array<BaseError>> {
    const taskErrors = (await validate(JobTaskInputSchema.name, task))
      .map((error: ValidationError) => new TaskError(error, task))
      .map((baseError: BaseError) => {
        return this.translateError(baseError);
      });

    taskErrors.push(...this.validateMissingTaskUser(task));
    taskErrors.push(...this.validateMissingWorkflowActions(task));

    for (const action of task.actions.filter((a: JobTaskActionInput) => !a.deleteFlag)) {
      taskErrors.push(...await this.validateAction(jobDefinition, task, action))
    }

    return taskErrors;
  }

  private async validateAction(jobDefinition: JobDefinitionInput,
                               task: JobTaskInput,
                               action: JobTaskActionInput): Promise<Array<BaseError>> {
    const actionErrors = (await validate(JobTaskActionInputSchema.name, action))
      .map((error: ValidationError) => new ActionError(error, task, action))
      .map((baseError: BaseError) => {
        return this.translateError(baseError);
      });

    actionErrors.push(...await this.validateActionDocument(jobDefinition, task, action));

    for (const parameter of action.parameters) {
      actionErrors.push(...await this.validateActionParameter(task, action, parameter));
    }

    return actionErrors;
  }

  private async validateActionParameter(task: JobTaskInput,
                                        action: JobTaskActionInput,
                                        parameter: JobTaskActionParameterInput): Promise<Array<BaseError>> {
    return (await validate(JobTaskActionParameterInputSchema.name, parameter))
      .map((error: ValidationError) => new ActionParameterError(error, task, action, parameter))
      .map((baseError: BaseError) => {
        return this.translateError(baseError);
      });
  }

  private async validateDocument(document: JobDocumentInput, result: Array<BaseError>): Promise<Array<BaseError>> {
    return (await validate(JobDocumentInputSchema.name, document))
      .map((error: ValidationError) => new DocumentError(error, document))
      .map((baseError: BaseError) => {
        return this.translateError(baseError);
      });
  }

  showValidationDialog(jobDefinitionId: number, errors: Array<BaseError>): Observable<string | undefined> {
    const dialogRef = this.matDialog.open(ValidationDialogComponent);
    dialogRef.componentInstance.errors = errors;

    return dialogRef.afterClosed().map((result: BaseError) => {
      if (result instanceof JobDefinitionError) {
        return `jobdefinitions/${ jobDefinitionId }`;
      }

      if (result instanceof DocumentError) {
        return `jobdefinitions/${ jobDefinitionId }/documents/${result.document.id}`;
      }

      if (result instanceof TaskError) {
        return `jobdefinitions/${ jobDefinitionId }/tasks/${result.task.id}`;
      }

      if (result instanceof ActionError) {
        return `jobdefinitions/${ jobDefinitionId }/tasks/${result.task.id}/actions/${result.action.id}`;
      }


      if (result instanceof ActionParameterError) {
        return `jobdefinitions/${ jobDefinitionId }/tasks/${result.task.id}/actions/${result.action.id}`;
      }

    });
  }

  private validateMissingWorkflowActions(task: JobTaskInput): Array<TaskError> {
    const result: Array<TaskError> = [];

    if (!task.actions.find((action: JobTaskActionInput) => {
        return this.actionTypeService.isFinishing(action.actionType) && !action.deleteFlag;
      })) {
      const error = new TaskError({
        property: '',
        value: '',
        constraints:
          {
            missingWorkflowActions: this.t.instant('frigg.noWfTaskOnAction')
          },
        target: task,
        children: []
      }, task);
      error.critical = false;

      result.push(error);
    }

    return result;
  }

  private validateMissingTasks(job: JobDefinitionInput): Array<JobDefinitionError> {
    const result: Array<JobDefinitionError> = [];

    if (!job.tasks.find((task: JobTaskInput) => {
        return !task.deleteFlag;
      })) {
      const error = new JobDefinitionError({
        property: '',
        value: '',
        constraints:
          {
            missingTasks: this.t.instant('frigg.noTaskOnJob')
          },
        target: job,
        children: []
      }, job);
      error.critical = false;

      result.push(error);
    }

    return result;
  }

  private validateMissingTaskUser(task: JobTaskInput): Array<TaskError> {
    const result: Array<TaskError> = [];

    if (!task.userId) {
      const error = new TaskError({
        property: '',
        value: '',
        constraints:
          {
            missingTasks: this.t.instant('frigg.noUserOnTask')
          },
        target: task,
        children: []
      }, task);
      error.critical = false;

      result.push(error);
    }

    return result;
  }

  private validateActionDocument(jobDefinition: JobDefinitionInput,
                                 task: JobTaskInput,
                                 action: JobTaskActionInput): Array<TaskError> {
    const result: Array<TaskError> = [];

    if (this.actionTypeService.isDocumentAction(action.actionType)) {
      const documentParameter = action.parameters
        .find((p: JobTaskActionParameterInput) => p.description === 'documentId');

      if (!documentParameter ||
        !documentParameter.value ||
        !jobDefinition.documents
          .find((d: JobDocumentInput) => d.id.toString() === documentParameter.value && !d.deleteFlag)) {
        const error = new ActionError({
          property: '',
          value: '',
          constraints:
            {
              missingTasks: this.t.instant('frigg.noDocumentOnAction')
            },
          target: action,
          children: []
        }, task, action);
        error.critical = false;

        result.push(error);
      }
    }

    return result;
  }

  translateError(error: BaseError): BaseError {
    const validationError = error.error;

    if (validationError.constraints.isNotEmpty && validationError.property === 'name') {
      if (error instanceof JobDefinitionError) {
        error.error.constraints.isNotEmpty = this.t.instant('frigg.jobNameIsRequired');
      } else if (error instanceof TaskError) {
        error.error.constraints.isNotEmpty = this.t.instant('frigg.taskNameIsRequired');
      } else if (error instanceof DocumentError) {
        error.error.constraints.isNotEmpty = this.t.instant('frigg.documentNameIsRequired');
      } else if (error instanceof ActionError) {
        error.error.constraints.isNotEmpty = this.t.instant('frigg.actionNameIsRequired');
      }
    }
    return error;
  }
}



