import { Component, Input } from '@angular/core';
import { last } from 'lodash';
import { IOption } from 'ng-select';
import { CreateJobDocumentRevisionInput, JobDocumentInput, JobDocumentRevisionDto } from '../../../../proxies';
import { JobDefinitionDocumentService } from '../job-definition-document.service';

interface DocumentType {
  value: string;
  description: string;
}

@Component({
  selector: 'frigg-document-tile',
  templateUrl: 'document-tile.component.html',
  styleUrls: [ 'document-tile.component.scss' ]
})
export class DocumentTileComponent {
  @Input() document: JobDocumentInput;

  get currentRevision(): CreateJobDocumentRevisionInput | JobDocumentRevisionDto | null {
    const revisions: JobDocumentRevisionDto[] = (this.document as any).revisions;
    if (this.document.newRevision) {
      return this.document.newRevision;
    } else if (revisions && revisions.length > 0) {
      return (last(revisions) as JobDocumentRevisionDto);
    }

    return null;
  }

  constructor(private documentService: JobDefinitionDocumentService) {
  }

  getDocumentTypeDescription(value: string): string {
    const documentType: IOption | undefined = this.documentService.documentTypes.find(
      (type: IOption) => type.value === value);

    return documentType ?
      documentType.label :
      'Other';
  }
}



