import { ValidationError } from 'class-validator'
import { JobTaskInput } from '../../../../proxies';
import { BaseError } from './base-error';

export class TaskError extends BaseError {
  task: JobTaskInput;

  constructor(error: ValidationError, task: JobTaskInput) {
    super();
    this.task = task;
    this.error = error;
  }
}



