import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { minBy } from 'lodash';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { DialogService } from '../../../core/dialog/dialog.service';
import { RadioDialogValue } from '../../../core/dialog/radio/radio-dialog.model';
import {
  ActionTypeDto,
  ActionTypeParameterDto,
  JobDefinitionDto,
  JobDocumentDto,
  JobTaskActionDto,
  JobTaskActionParameterDto,
  JobTaskActionParameterInput,
  JobTaskDto
} from '../../../proxies';
import { JobDefinitionDetailService } from '../job-definition-detail.service';
import { JobDefinitionTaskService } from '../task/job-definition-task.service';
import { ActionTypeListService } from './action-type-list.service';

@Injectable()
export class TaskActionService {
  private activeTaskActionSubject: BehaviorSubject<JobTaskActionDto> = new BehaviorSubject(new JobTaskActionDto());

  taskActionForm: FormGroup;
  documentList: { value: string, label: string }[] = [];
  userList: { value: string, label: string }[] = [];
  toTaskList: { value: string, label: string }[] = [];

  constructor(private jobDefinitionService: JobDefinitionDetailService,
              private taskService: JobDefinitionTaskService,
              private formBuilder: FormBuilder,
              private dialog: DialogService,
              private t: TranslateService,
              private actionTypeListService: ActionTypeListService) {

    this.activeTaskActionSubject.subscribe((action: JobTaskActionDto) => {
      this.initTaskActionForm(action);
    });
  }

  get afterSuccessfulSave(): Observable<any> {
    return this.jobDefinitionService.afterSuccessfulSave;
  }

  get activeJobDefinition(): JobDefinitionDto {
    return this.jobDefinitionService.activeJobDefinition;
  }

  get activeTaskAction(): JobTaskActionDto {
    return this.activeTaskActionSubject.getValue();
  }

  set activeTaskAction(taskAction: JobTaskActionDto) {
    this.activeTaskActionSubject.next(taskAction);
  }

  get activeJobTask(): JobTaskDto {
    return this.taskService.activeJobTask;
  }

  set activeJobTask(value: JobTaskDto) {
    this.taskService.activeJobTask = value;
  }

  get toTaskPlaceholder(): string {
    return this.activeTaskAction.actionType === 'forward'
      ? this.t.instant('frigg.nextTask')
      : this.t.instant('frigg.previousTask');
  }

  validateJobDefinition(): void {
    this.jobDefinitionService.validateJobDefinition();
  }

  initTaskActionForm(action: JobTaskActionDto): void {
    const formConfig: any = {
      name: [ '', Validators.required ],
      requiredAction: [ false ],
      actionComment: [ 1 ]
    };
    const patchValues: any = {
      name: this.activeTaskAction.name,
      requiredAction: this.activeTaskAction.requiredAction,
      actionComment: this.activeTaskAction.actionComment || this.activeTaskAction.actionComment === 0 ?
        this.activeTaskAction.actionComment.toString() :
        this.activeTaskAction.actionComment
    };
    this.initParameterFields(action, formConfig, patchValues);

    this.taskActionForm = this.formBuilder.group(formConfig);
    this.taskActionForm.patchValue(patchValues);

    if (this.jobDefinitionService.readOnly) {
      this.taskActionForm.disable();
    }

    this.taskActionForm.valueChanges.subscribe(() => {
      this.onFormValueChanges();
    });
  }

  createTaskAction(radioList: RadioDialogValue[]): Observable<JobTaskActionDto | null> {
    return this.dialog.radio(this.t.instant('frigg.newActionHeader'), radioList)
      .map((radioResult: { value: any, description: string }) => {
        if (!radioResult) {
          return null;
        }

        const action: JobTaskActionDto = new JobTaskActionDto();
        const actions: Array<JobTaskActionDto> = this.activeJobTask.actions;
        const minActionId: number = actions.length ?
          (minBy(actions, (a: JobTaskActionDto) => a.id) as JobTaskActionDto).id :
          -1;

        action.id = minActionId < 0 ? minActionId - 1 : -1;
        action.name = radioResult.description;
        action.description = '';
        action.actionType = radioResult.value;
        action.actionComment = 1;
        action.parameters = this.createActionParameters(action);

        this.jobDefinitionService.tasksHaveChanges = true;
        this.activeJobTask.actions.push(action);

        this.jobDefinitionService.tasksHaveChanges = true;

        return action;
      }).first();
  }

  private initParameterFields(action: JobTaskActionDto, formConfig: any, patchValues: any): void {
    if (this.actionHasParameter(action, 'documentId')) {
      this.initDocumentIdField(formConfig, patchValues);
    }
    if (this.actionHasParameter(action, 'toTaskId')) {
      this.initToTaskIdField(formConfig, patchValues);
    }
    if (this.actionHasParameter(action, 'url')) {
      this.initUrlField(formConfig, patchValues);
    }
  }

  private initDocumentIdField(formConfig: any, patchValues: any): void {
    formConfig.documentId = [ '', Validators.required ];
    patchValues.documentId = this.getParameterValue('documentId');
    this.documentList = this.activeJobDefinition.documents
      .filter((doc: JobDocumentDto) => !doc.deleteFlag)
      .map((doc: JobDocumentDto) => {
        return {value: doc.id.toString(), label: doc.name};
      });
  }

  private initToTaskIdField(formConfig: any, patchValues: any): void {
    formConfig.toTaskId = [ '' ];
    patchValues.toTaskId = this.getParameterValue('toTaskId');
    this.toTaskList = this.activeJobDefinition.tasks
      .filter((t: JobTaskDto) => !t.deleteFlag && t.id !== this.activeJobTask.id)
      .map((t: JobTaskDto) => {
        return {value: t.id.toString(), label: t.name};
      });
  }

  private initUrlField(formConfig: any, patchValues: any): void {
    formConfig.url = [ '' ];
    patchValues.url = this.getParameterValue('url');
  }

  createNonFinishingTaskAction(): Observable<JobTaskActionDto | null> {
    const radioList: RadioDialogValue[] = this.actionTypeListService.actionTypeList
      .filter((actionType: ActionTypeDto) => actionType.category === '1')
      .map((actionType: ActionTypeDto) => {
        return {value: actionType.id, description: this.t.instant('frigg.' + actionType.id)};
      });

    return this.createTaskAction(radioList);
  }

  createFinishingTaskAction(): Observable<JobTaskActionDto | null> {
    const radioList: RadioDialogValue[] = this.actionTypeListService.actionTypeList
      .filter((actionType: ActionTypeDto) => actionType.category === '2')
      .map((actionType: ActionTypeDto) => {
        return {value: actionType.id, description: this.t.instant('frigg.' + actionType.id)};
      });

    return this.createTaskAction(radioList);
  }

  actionHasParameter(action: JobTaskActionDto, parameter: string): boolean {
    const actionType: ActionTypeDto | undefined = this.actionTypeListService.actionTypeList
      .find((a: ActionTypeDto) =>
        a.id === action.actionType
      );

    const parameters = actionType && actionType.parameters
      .filter((p: ActionTypeParameterDto) =>
        p.description === parameter);

    return parameters !== undefined && parameters.length > 0;
  }

  createActionParameters(action: JobTaskActionDto): Array<JobTaskActionParameterDto> {
    const actionType: ActionTypeDto | undefined = this.actionTypeListService.actionTypeList
      .find((a: ActionTypeDto) =>
        a.id === action.actionType
      );

    if (!actionType) {
      return [];
    }

    return actionType.parameters
      .map((p: ActionTypeParameterDto, index: number) => {
        const parameter: JobTaskActionParameterInput = new JobTaskActionParameterInput();
        parameter.id = (index + 1) * -1;
        parameter.description = p.description;
        parameter.value = '';

        return parameter;
      });
  }

  updateTabs(route: ActivatedRoute): void {
    const taskId: number = route.snapshot.params[ 'taskId' ];
    const actionId: number = route.snapshot.params[ 'actionId' ];

    const tabs: any [] = [
      this.jobDefinitionService.tabs[ 0 ],
      {
        label: this.t.instant('frigg.tasks'),
        routerLink: [ 'tasks' ]
      }
    ];

    if (taskId) {
      tabs.push({
        label: this.activeJobTask.name || this.t.instant('frigg.newTask'),
        routerLink: [ 'tasks/' + taskId ]
      });
    }

    if (actionId) {
      tabs.push({
        label: this.activeTaskAction.name || this.t.instant('frigg.newAction'),
        routerLink: [ 'tasks/' + taskId + '/actions/' + actionId ]
      });
    }

    this.jobDefinitionService.tabs = tabs;
  }

  setActiveTaskAction(taskId: number, actionId: number): JobTaskActionDto | undefined {
    const task: JobTaskDto | undefined = this.taskService.setActiveTask(taskId);
    if (!task) {
      return undefined;
    }

    const action: JobTaskActionDto | undefined = task.actions.find((a: JobTaskActionDto) => {
      return a.id === +actionId;
    });

    if (action) {
      this.activeTaskActionSubject.next(action);
    }

    return action;
  }

  toggleDeleteFlagAction(action: JobTaskActionDto): void {
    action.deleteFlag = !action.deleteFlag;
    this.jobDefinitionService.tasksHaveChanges = true;
    this.validateJobDefinition();
  }

  hasParameter(parameter: string): boolean {
    return this.actionHasParameter(this.activeTaskAction, parameter);
  }

  getParameterValue(parameter: string): any {
    const actionParameter: JobTaskActionParameterDto | undefined = this.activeTaskAction.parameters
      .find((p: JobTaskActionParameterDto) =>
        p.description === parameter
      );

    return actionParameter !== undefined ?
      actionParameter.value :
      undefined;
  }

  isFinishingAction(action: JobTaskActionDto): boolean {
    const actionType: ActionTypeDto | undefined = this.actionTypeListService.actionTypeList
      .find((a: ActionTypeDto) =>
        a.id === action.actionType
      );

    return !(actionType && actionType.category === '2');
  }

  setParameterValue(parameter: string, value: any): any {
    let actionParameter: JobTaskActionParameterDto | undefined = this.activeTaskAction.parameters
      .find((p: JobTaskActionParameterDto) =>
        p.description === parameter
      );

    if (actionParameter === undefined) {
      actionParameter = new JobTaskActionParameterDto();
      const parameters: Array<JobTaskActionParameterDto> = this.activeTaskAction.parameters;
      const minParameter: number = parameters.length ?
        (minBy(parameters, (a: JobTaskActionParameterDto) => a.id) as JobTaskActionParameterDto).id :
        -1;

      actionParameter.id = minParameter < 0 ? minParameter - 1 : -1;

      actionParameter.description = parameter;
      this.activeTaskAction.parameters.push(actionParameter);
    }

    actionParameter.value = value;
  }

  private onFormValueChanges(): void {
    this.activeTaskAction.name = this.taskActionForm.controls.name.value;
    this.activeTaskAction.requiredAction = this.taskActionForm.controls.requiredAction.value;
    this.activeTaskAction.actionComment = +this.taskActionForm.controls.actionComment.value;


    if (this.taskActionForm.contains('documentId')) {
      this.setParameterValue('documentId', this.taskActionForm.controls.documentId.value ?
        this.taskActionForm.controls.documentId.value : '');
    }
    if (this.taskActionForm.contains('toTaskId')) {
      this.setParameterValue('toTaskId', this.taskActionForm.controls.toTaskId.value ?
        this.taskActionForm.controls.toTaskId.value : '');
    }
    if (this.taskActionForm.contains('url')) {
      this.setParameterValue('url', this.taskActionForm.controls.url.value ?
        this.taskActionForm.controls.url.value : '');
    }

    this.jobDefinitionService.tasksHaveChanges = true;
    this.jobDefinitionService.tabs[ 3 ].label = this.activeTaskAction.name;
    this.jobDefinitionService.onChange();
  }
}




