import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../core/core.service';
import { FriggResponse, JobDefinitionDto, JobService } from '../proxies/';

@Injectable()
export class JobDefinitionStatusService {

  constructor(private jobService: JobService,
              private core: CoreService) {
  }

  startJob(jobId: number): Observable<JobDefinitionDto | null> {
    return this.executeActivity(this.jobService.activateJob(jobId), 'frigg.jobIsStarted')
  }

  continueJob(jobId: number): Observable<JobDefinitionDto | null> {
    return this.executeActivity(this.jobService.unPauseJob(jobId), 'frigg.jobIsContinued');
  }

  pauseJob(jobId: number): Observable<JobDefinitionDto | null> {
    return this.executeActivity(this.jobService.pauseJob(jobId), 'frigg.jobIsPaused');
  }

  restartJob(jobId: number): Observable<JobDefinitionDto | null> {
    return this.executeActivity(this.jobService.restartJob(jobId), 'frigg.jobIsRestarted');
  }

  abortJob(jobId: number): Observable<JobDefinitionDto | null> {
    return this.executeActivity(this.jobService.abortJob(jobId), 'frigg.jobIsAborted');
  }

  private executeActivity(activity: Observable<FriggResponse<JobDefinitionDto>>,
                          successMessage: string): Observable<JobDefinitionDto | null> {
    return activity
      .map((response: FriggResponse<JobDefinitionDto>) => {
        if (response.success) {
          this.core.toast.success(this.core.translate(successMessage, {name: response.result.name}));
          return response.result;
        } else {
          return null;
        }
      });
  }
}



