import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { JobDefinitionListDto } from '../../../proxies';


@Component({
  selector: 'frigg-job-definition-tile',
  templateUrl: 'job-definition-tile.component.html',
  styleUrls: [ 'job-definition-tile.component.scss' ]
})
export class JobDefinitionTileComponent {
  @Input() job: JobDefinitionListDto;
  @Output() startClick: EventEmitter<any> = new EventEmitter();
  @Output() pauseClick: EventEmitter<any> = new EventEmitter();
  @Output() restartClick: EventEmitter<any> = new EventEmitter();
  @Output() problemClick: EventEmitter<any> = new EventEmitter();
  @Output() continueClick: EventEmitter<any> = new EventEmitter();
  @Output() abortClick: EventEmitter<any> = new EventEmitter();

  constructor(private t: TranslateService) {
  }

  getStatusText(job: JobDefinitionListDto): string {
    return `${ this.t.instant('frigg.status_' + job.status)}`;
  }

  canStart(job: JobDefinitionListDto): boolean {
    return job.status === 'Ready';
  }

  canPause(job: JobDefinitionListDto): boolean {
    return [ 'Active' ].indexOf(job.status) > -1;
  }

  canRestart(job: JobDefinitionListDto): boolean {
    return [ 'Completed' ].indexOf(job.status) > -1
      || [ 'Aborted' ].indexOf(job.status) > -1;
  }

  isOnHold(job: JobDefinitionListDto): boolean {
    return job.status === 'OnHold';
  }

  hasProblems(job: JobDefinitionListDto): boolean {
    return job.status === 'Draft';
  }

  onStartClick(event: Event): void {
    event.cancelBubble = true;
    this.startClick.next();
  }

  onPauseClick(event: Event): void {
    event.cancelBubble = true;
    this.pauseClick.next();
  }

  onRestartClick(event: Event): void {
    event.cancelBubble = true;
    this.restartClick.next();
  }

  onProblemClick(event: Event): void {
    event.cancelBubble = true;
    this.problemClick.next();
  }

  onContinueClick(event: Event): void {
    event.cancelBubble = true;
    this.continueClick.next();
  }

  onAbortClick(event: Event): void {
    event.cancelBubble = true;
    this.abortClick.next();
  }

}




