import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';
import { ActionTypeListService } from './detail/action/action-type-list.service';
import { DocumentTileComponent } from './detail/document/list/document-tile.component';
import { JobDefinitionResolver } from './detail/resolver/job-definition-resolver.service';
import { JobDefinitionUserListResolver } from './detail/resolver/job-definition-userlist-resolver.service';
import { TaskTileComponent } from './detail/task/list/task-tile.component';
import { JobDefinitionValidationService } from './detail/validation/job-definition-validation.service';
import { ErrorTileComponent } from './detail/validation/validation-dialog/error-tile/error-tile.component';
import { ValidationDialogComponent } from './detail/validation/validation-dialog/validation-dialog.component';
import { JobDefinitionStatusService } from './job-definition-status.service';
import { JobDefinitionRoutingModule, routedComponents } from './job-definition.routing';
import { JobDefinitionListModule } from './list/job-definition-list.module';

@NgModule({
  imports: [
    SharedModule,
    JobDefinitionRoutingModule,
    JobDefinitionListModule
  ],
  declarations: [
    ...routedComponents,
    DocumentTileComponent,
    TaskTileComponent,
    ValidationDialogComponent,
    ErrorTileComponent
  ],
  providers: [
    JobDefinitionResolver,
    JobDefinitionUserListResolver,
    JobDefinitionValidationService,
    JobDefinitionStatusService,
    ActionTypeListService
  ],
  entryComponents: [
    ValidationDialogComponent
  ]
})
export class JobDefinitionModule {
}



