import { NgModule } from '@angular/core';
import { SharedModule } from '../../shared/shared.module';
import { JobDefinitionListComponent } from './job-definition-list.component';
import { JobDefinitionListResolver } from './resolver/job-definition-list-resolver.service';
import { JobDefinitionTileComponent } from './tile/job-definition-tile.component';

@NgModule({
  imports: [
    SharedModule
  ],
  declarations: [
    JobDefinitionListComponent,
    JobDefinitionTileComponent
  ],
  providers: [
    JobDefinitionListResolver
  ]
})
export class JobDefinitionListModule {
}



